.projects-display {
    margin-top: 0;
}

.complete-project {
    display: flex;
    flex-direction: column;
    /*box-shadow: 0 0 5px 2px #d6d6d6;*/
    justify-content: center;
    margin-right: 30px;
    margin-left: 30px;
    /*background: var(--white);*/
    /*border-radius: 7px;*/
}

.project-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 20px;
    max-width: 330px;
    min-width: 330px;
    min-height: 500px;
}

.project-card a {
    color: var(--black);
    padding: 1px;
    font-size: 20px;
    font-weight: bold;
}

.project-card a:hover {
    color: var(--secondary);
    transition: 0.2s;
}

