.project-name {
    text-align: left;
}

.project-links {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
    margin-top: 10px;
    margin-right: 30px;
}

.project-img img {
    width: 100%;
    height: auto;
    box-shadow: 2px 3px 5px #ababab;
    border-radius: 4px;
    margin-bottom: 1.5rem;
}

